.header {
  padding: 5px 0;
  @media (max-width: 767px) {
    position: relative;
  }
  &__logo {
    height: 52px;
    max-width: 145px;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: left center;
    }
  }
  &__nav {
    margin-left: auto;
    display: flex;
    align-items: center;
  }
  .nav {
    &__list {
      display: flex;
      @media (max-width: 767px) {
        position: absolute;
        right: -150vh;
        top: 0;
      }
      &-item {
        padding: 0 20px;
        &__link {
          font-size: 1.3rem;
          color: #fff;
          text-decoration: none;
          transition: 0.2s;
          position: relative;
          padding-top: 2px;
          &::after {
            content: "";
            display: flex;
            width: 0%;
            height: 2px;
            background-color: transparent;
            transition: 0.2s;
          }
          &:hover {
            span {
              transition: 0.2s;
              color: $secondary-font-color;
            }
            &::after {
              content: "";
              display: flex;
              width: 100%;
              height: 2px;
              background-color: $secondary-font-color;
              transition: 0.2s;
            }
          }
        }
      }
    }
  }
}
