.form {
  display: flex;
  flex-direction: column;
  width: 100%;
  border: 5px solid $main-font-color;
  padding: 40px;
  margin: 24px 0 48px;
  color: $main-font-color;
  &__row {
    display: flex;
    margin-bottom: 48px;
    @media (max-width: 1200px) {
      margin-bottom: 36px;
    }
    @media (max-width: 992px) {
      margin-bottom: 24px;
    }
    &:nth-last-child(1) {
      margin-bottom: 0;
    }
    &_align-center {
      align-items: center;
    }
  }
  &__col {
    display: flex;
    flex-direction: column;
    &_50 {
      width: 50%;
      &:nth-child(1) {
        padding-right: 40px;
        @media (max-width: 992px) {
          padding-right: 20px;
        }
      }
      &:nth-last-child(1) {
        padding-left: 40px;
        @media (max-width: 992px) {
          padding-left: 20px;
        }
      }
    }
  }
  &__input {
    border: 2px solid $main-font-color;
    color: $main-font-color;
    font-size: 1.3rem;
    padding: 16px;
    background-color: #000;
    margin-bottom: 48px;
    transition: 0.3s;
    @media (max-width: 1200px) {
      margin-bottom: 36px;
    }
    @media (max-width: 992px) {
      margin-bottom: 24px;
    }
    &:nth-last-child(1) {
      margin-bottom: 0;
    }
    &::placeholder {
      color: $main-font-color;
    }
    &:focus {
      border: 2px solid $secondary-font-color;
      outline: none;
      color: $secondary-font-color;
      &::placeholder {
        color: $secondary-font-color;
      }
    }
  }
  &__textarea {
    border: 2px solid $main-font-color;
    color: $main-font-color;
    font-size: 1.3rem;
    padding: 16px;
    background-color: #000;
    resize: none;
    height: 100%;
    transition: 0.3s;
    &::placeholder {
      color: $main-font-color;
    }
    &:focus {
      border: 2px solid $secondary-font-color;
      outline: none;
      color: $secondary-font-color;
      transition: 0.3s;
      &::placeholder {
        color: $secondary-font-color;
      }
    }
  }
  &__checkbox {
    display: block;
    border: 2px solid $main-font-color;
    width: 32px;
    height: 32px;
    position: relative;
    margin-right: 15px;
    cursor: pointer;
    &::after {
      content: "";
      display: none;
      width: 15px;
      height: 7px;
      position: absolute;
      border-left: 5px solid $main-font-color;
      border-bottom: 5px solid $main-font-color;
      top: 5px;
      left: 4px;
      transform: rotate(-45deg);
    }
    &-label {
      font-size: 1.3rem;
      cursor: pointer;
    }
    &-input {
      display: none;
      &:checked ~ .form__checkbox::after {
        display: block;
      }
    }
  }
  &__button {
    width: 250px;
    height: 48px;
    font-size: 1.3rem;
    background-color: #000;
    color: $main-font-color;
    text-align: center;
    margin: 0 auto;
    border: 2px solid $main-font-color;
    transition: 0.3s;
    cursor: pointer;
    &:hover,
    &:focus {
      outline: none;
      color: $secondary-font-color;
      border: 2px solid $secondary-font-color;
    }
  }
}
